<template>
  <div class="data">
    <div class="header">
      <ul>
        <li v-for="(item, index) in leagues" v-text="item.title" :class="curLeaguesIndex===index ? activeClass :''" @click="onCurrentLeague(item, index)"></li>
      </ul>
      <ol>
        <li v-for="(item,index) in types" v-text="item.title" :class="curType===index ? activeClass : ''" @click="onRank(item,index)"></li>
      </ol>
    </div>
    <div class="rank">
      <scroll ref="scroll" class="rank-content" :data="datas">
        <div>
          <score :score="datas" v-if="0==curType && datas.length" @updateScroll="freshScroll"></score>
          <shooter :shooter="datas" v-if="1==curType && datas.length" @updateScroll="freshScroll"/>
          <assist :assist="datas" v-if="2==curType && datas.length" @updateScroll="freshScroll"/>
          <schedule :schedule="datas" v-if="3==curType && datas.length" @updateScroll="freshScroll"/>
        </div>
        <loading v-if="!datas.length"></loading>
      </scroll>
    </div>
  </div>
</template>

<script>
  import Loading from 'components/loading/loading'
  import Scroll from 'components/scroll/scroll'
  import Score from 'pages/data/score'
  import Shooter from 'pages/data/shooter'
  import Assist from 'pages/data/assist'
  import Schedule from 'pages/data/schedule'
  import {getRank} from 'api/data'
  export default {
    data() {
      return {
        leagues: [{title:'',league:51,id: 39713,gameweek: 30},{title:'',league:8, id:41547,gameweek: 38},{title:'',league:13,id: 42011,gameweek: 38},{title:'',league:7,id: 41509,gameweek: 38},{title:'',league:9,id:41485,gameweek: 38},{title:'',league:148,id:39175,gameweek: 30}],
        types: [{title:'',type:'team_ranking'},{title:'',type:'goal_ranking'},{title:'',type:'assist_ranking'},{title:'',type:'schedule'}],
        curLeague: {title:'',league:51,id: 39713,gameweek: 30},
        curLeaguesIndex: 0,
        curType: 0,
        activeClass: 'on',
        scores: [],
        shooters: [],
        assists: [],
        schedules: []
      }
    },
    computed: {
      datas() {
        let obj = {
          0: this.scores,
          1: this.shooters,
          2: this.assists,
          3: this.schedules
        }
        return obj[this.curType]
      }
    },
    created() {
      this._getRank('team_ranking', this.curLeague)
    },
    methods: {
      // DOM
      freshScroll() {
        this.$refs.scroll.refresh()
      },
      // 
      onCurrentLeague(item,index) {
        this._clearRank()
        this.curLeaguesIndex = index
        this.curLeague = item
        this.curType = 0
        this._getRank('team_ranking', item)
      },
      // 
      onRank(item,index){
        this._clearRank()
        this.curType = index
        this._getRank(item.type, this.curLeague)
      },
      // 
      _getRank(type ,league) {
        getRank(type,league).then((res)=>{
          console.log(res)
          if(res.statusText === 'OK'){
            let obj = {
              'team_ranking':() => {
                this.scores = res.data[0].rankings
              },
              'goal_ranking':() => {
                this.shooters = res.data
              },
              'assist_ranking':() => {
                this.assists = res.data
              },
              'schedule':() => {
                this.schedules = res.data.matches
              }
            }
            if (typeof obj[type] !== 'function') {
               throw new Error('Invalid type.');
             }
            obj[type]()
          }
        })
      },
      // 
      _clearRank(){
        this.scores = []
        this.shooters = []
        this.assists = []
        this.schedules = []
      }
    },
    components: {
      Score,
      Shooter,
      Assist,
      Schedule,
      Loading,
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/variable"
.data
  position: absolute
  width: 100%
  top: 6rem
  bottom: 0
  ul
  	display: flex
  	font-size: 1rem
  	background: $color-lightgray
  	color: $color-g
  	li
      flex: 1
      height: 2.5rem
      line-height: 2.5rem
      box-sizing: border-box
      transition all .2s ease
    .on
      border-bottom: 3px solid $color-g
  ol
    display:flex
    background: #3c3b3b
    font-size: 1rem
    color: #ffffff
    box-sizing: border-box
    li
      flex: 1
      height: 2.5rem
      line-height: 2.5rem
      box-sizing: border-box
      transition all .2s ease
    .on
      color $color-blue
  .rank
    width: 100%
    .rank-content
      height: 100%
      overflow: hidden
</style>



