<template>
  <div class="shooter">
  	<dl>
  		<dt>
  		    <div class="type3"></div>
  			<div class="type1"></div>
  			<div class="type1"></div>
  			<div class="type3"></div>
  		</dt>
  		<dd v-for="(item, index) in shooter">
  			<div class="type3" v-text="index+1"></div>
  			<div class="type1" v-text="item.name"></div>
  			<div class="type1">
  				<img :src="'//img.dongqiudi.com/data/pic/'+item.team_id+'.png'"/>
  				<span v-html="item.team_name"></span>
  			</div>
  			<div class="type3" v-text="item.count"></div>
  		</dd>
  	</dl>
  </div>
</template>

<script>
  export default {
  	props: {
  	  shooter: {
  		type: Array,
  		required: true
  	  }
  	},
  	mounted() {
      setTimeout(() => {
      	this.$emit('updateScroll')
      },20)
  	}
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/variable"
.shooter
  h3
  	text-align: left
  p
  	text-align: left
  dl
  	background: $color-lightgray
  	font-size: 1rem
	dt 
    display: flex 
    padding: .5rem 0 
    border-bottom: 1px solid #dadada
	  .type1
	  	flex: 4
	  	text-align: center
	  .type3
	  	flex: 2
	dd
	  display: flex
	  border-bottom: 1px solid $color-border
	  padding: 1rem 0
	  font-size: 1rem
	  position: relative
	  .type1
	  	flex: 4
	  	text-align: center
	  	img
	  	  width: 1.4rem
	  	  position: absolute
	  	  top: 0.8rem
	  	  left: 3.5rem
	  .type3
	  	flex: 2
</style>



